.searchForm input {
    padding: 10px 15px;
    font-size: 20px;
    font-weight: 700;
    border: none;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    outline: none;
    resize: none;
}

.searchForm button {
    padding: 10px 5px;
    margin-left: 10px;
    font-size: 20px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: rgb(162, 0, 211);
    color: white;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
}