.fullCard {
    width: 600px;
    height: 300px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, .5);
}

.fullCard h3 {
    text-align: center;
    font-weight: 300;
    font-size: 34px;
    padding-top: 20px;
}

.fullCard span {
    display: block;
    font-weight: 300;
    font-size: 34px;
    padding-left: 20px;
}

.temperature {
    padding-left: 50px;
}

.minMaxBlock {
    display: flex;
}

.icon {
    width: 128px;
    height: 128px;
    float: right;
    padding-right: 60px;
    padding-top: 10px;
}