.card {
    background-color: rgb(162, 0, 211);
    display: flex;
    justify-content: space-between;
    text-decoration: none;
    align-items: center;
    color: white;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    width: 450px;
    transition: 0.2s ease-out;
    transition-property: transform;
}

.card:hover {
    transform: scale(1.1);
}

.card h3 {
    font-weight: 300;
    padding-left: 10px;
    font-size: 26px;
    line-height: 20px;
}

.weatherDataBlock {
    display: flex;
    align-items: center;
}

.weatherDataBlock img {
    height: 50px;
    width: 50px;
    padding-right: 20px;
}

.weatherDataBlock span {
    font-weight: 300;
    font-size: 26px;
    padding-right: 10px;
}

.navLink {
    text-decoration: none;
}

.actions {
    float: right;
    margin-top: 10px;
}

.refreshButton {
    margin-right: 5px;
    transition: 0.3s ease-out;
    transition-property: transform;
}

.refreshButton:hover {
    transform: rotateZ(360deg);
}

.removeButton {
    transition: 0.3s ease-out;
    transition-property: transform;
}

.removeButton:hover {
    transform: rotate(360deg); 
}


